<!--  -->
<template>
  <el-dialog
    v-if="dialogVisible"
    :title="popupData.title"
    :center="true"
    :visible.sync="dialogVisible"
    :destroy-on-close="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :width="popupData.width"
    @close="closeDialog"
  >
    <div class="m-Content">
      <!--  -->
      <div class="userLogin" v-if="accountObj.userLogin">
        <input type="text" name id placeholder="" />
        <input type="text" name id placeholder="" />
        <button></button>
        <div class="otherLogin clearfix">
          <span @click="jump(7)"></span>
          <div class="clearfix">
            <font @click="jump(2)"></font>
            <span>|</span>
            <font @click="jump(3)">?</font>
          </div>
        </div>
      </div>
      <!--  -->

      <!--  -->
      <div class="iphoneLogin s-code" v-if="iphoneObj.iphoneLogin">
        <input type="text" name id placeholder="" />
        <div>
          <input type="text" name id placeholder="" />
          <template v-if="comm.sendCodeFlag">
            <button @click="sendCodeFun" class="sendVcode"></button>
          </template>
          <template v-else>
            <button disabled class="sendVcode active">{{comm.sec}}</button>
          </template>
        </div>
        <p ref="verify" class="verify middle"></p>
        <button @click="loginBtnFun"></button>
        <div class="otherLogin clearfix">
          <span @click="jump(4)"></span>
          <div class="clearfix">
            <font @click="jump(2)"></font>
          </div>
        </div>
      </div>
      <!--  -->

      <!--  -->
      <div class="registerObj s-code" v-if="registerObj.registerFlag">
        <input type="text" name id placeholder="" />
        <div>
          <input type="text" name id placeholder="" />
          <template v-if="comm.sendCodeFlag">
            <button @click="sendCodeFun" class="sendVcode"></button>
          </template>
          <template v-else>
            <button disabled class="sendVcode active">{{comm.sec}}</button>
          </template>
        </div>
        <p ref="verify" class="verify middle"></p>
        <button @click="jump"></button>
        <div class="otherLogin clearfix">
          <span>
            
            <font></font>
          </span>
          <span>
            
            <font @click="jump(4)"></font>
          </span>
        </div>
      </div>
      <!--  -->

      <!--  -->
      <div class="resetWordObj s-code" v-if="resetWordObj.resetWordFlag">
        <input type="text" name id placeholder="" />
        <div>
          <input type="text" name id placeholder="" />
          <template v-if="comm.sendCodeFlag">
            <button @click="sendCodeFun" class="sendVcode"></button>
          </template>
          <template v-else>
            <button disabled class="sendVcode active">{{comm.sec}}</button>
          </template>
        </div>
        <p ref="verify" class="verify middle"></p>
        <input type="text" name id placeholder="" />
        <button @click="loginBtnFun"></button>
      </div>
      <!--  -->

      <!-- -->
      <div class="newAccObj" v-if="newAccObj.newAccFlag">
        <h3></h3>
        <input type="text" name id placeholder="(3-20)" />
        <input type="text" name id placeholder="8-30" />
        <input type="text" name id placeholder="" />
        <button></button>
        <p></p>
      </div>
      <!--  -->

      <div class="msgsuccessObj" v-if="msgsuccessObj.msgsuccessFlag">
        <img src="@/assets/image/loginsuccess.png" alt />
        <!-- <p>...</p> -->
        <p></p>
        <button></button>
      </div>
    </div>
  </el-dialog>
</template>

<script>
//jsjsjson
//import  from '';
import vueEvent from "../../model/vueEvent";
export default {
  //import

  components: {},
  data() {
    //
    return {
      popupData: {}, //
      dialogVisible: false, //
      comm: {
        sec: 60, //
        sendCodeFlag: true, //
        timer: [] //
      },
      //
      accountObj: {
        userLogin: true //
      },
      //
      iphoneObj: {
        iphoneLogin: false //
        // verCodeFlag: true // 
      },
      //
      registerObj: {
        registerFlag: false //
      },
      //
      newAccObj: {
        newAccFlag: false //
      },
      // 
      resetWordObj: {
        resetWordFlag: false //
      },
      //
      msgsuccessObj: { msgsuccessFlag: false }
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    /**
     * 
     */
    jump(sign) {
      this.clearNUll();
      switch (sign) {
        case 1: // 
          this.iphoneObj.iphoneLogin = true;
          break;
        case 2: //   // 
          this.popupData.title = "";
          this.registerObj.registerFlag = true;
          break;
        case 3: //  
          this.popupData.title = "";
          this.resetWordObj.resetWordFlag = true;
          break;
        case 4: //   // 
          this.accountObj.userLogin = true;
          break;
        case 5: // 
          break;
        case 6: //
          this.popupData.title = "";
          this.newAccObj.newAccFlag = true;
          break;
        case 7: // 
          this.popupData.title = "";
          this.msgsuccessObj.msgsuccessFlag = true;
          break;
      }
    },
    // 
    sendCodeFun() {
      if (this.comm.sendCodeFlag) {
        this.comm.sendCodeFlag = false;
        clearInterval(this.comm.timer[0]);
        this.comm.sec = 60;
        this.comm.timer[0] = setInterval(() => {
          this.comm.sec--;
          if (this.comm.sec == 0) {
            clearInterval(this.comm.timer[0]);
            this.comm.sendCodeFlag = true;
          }
        }, 1000);
      }
    },

    loginBtnFun() {
      this.iphoneObj.verCodeFlag = false;
      this.$refs.verify.style.display = "block";
      this.$refs.verify.innerText = "";
    },

    clearNUll() {
      clearInterval(this.comm.timer[0]);
      this.accountObj = {};
      this.registerObj = {};
      this.iphoneObj = {};
      this.resetWordObj = {};
      this.newAccObj = {};
      this.msgsuccessObj = {};
      this.popupData.title = "";
    },
    //
    closeDialog() {
      console.log(1);

      this.dialogVisible = false;
      this.clearNUll();
      this.popupData = {};
      this.accountObj.userLogin = true;
    }
  },
  // - this
  created() {},
  // - DOM
  mounted() {
    vueEvent.$on("popupDataFun", val => {
      this.popupData = val.popupData;
      this.dialogVisible = val.dialogVisible;
    });
  },
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
>>> .el-dialog__body {
  height: auto;
  overflow: auto;
  padding: 0;
}
>>> .el-dialog {
  margin: 0 !important;
  position: absolute !important;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
>>> .el-dialog__title {
  font-size: 20px;
  color: #333;
  font-weight: 600;
  display: block;
  margin-top: 30px;
  margin-bottom: 30px;
}
.m-Content {
  width: 358px;
  margin: 0 auto;

  // 
  input {
    border-radius: 3px;
    border: 1px solid #ccc;
    width: 358px;
    height: 50px;
    color: #333;
    margin-bottom: 14px;
    padding: 5px 10px;
    box-sizing: border-box;
    &::placeholder {
      color: #999;
    }
  }
  button {
    width: 358px;
    height: 50px;
    border-radius: 3px;
    border: 1px solid #ccc;
    background: #2878ff;
    font-size: 14px;
    margin-bottom: 20px;
    box-sizing: border-box;
    color: #fff;
  }
  .userLogin {
    margin-bottom: 70px;
    .otherLogin {
      font-size: 0;
      color: #666;
      & > span {
        font-size: 14px;
        cursor: pointer;

        &:hover {
          color: #2878ff;
        }
      }
      & > div {
        float: right;
        font-size: 14px;
        font {
          cursor: pointer;
          &:hover {
            color: #2878ff;
          }
        }
        span {
          margin: 0 5px;
        }
      }
    }
  }
  .s-code {
    width: 358px;
    margin: 0 auto;
    margin-bottom: 70px;
    & > div:nth-of-type(1) {
      font-size: 0;
      overflow: hidden;
      & > input {
        width: 210px;
        height: 50px;
      }
      .sendVcode {
        width: 130px;
        height: 50px;
        margin-left: 18px;
        margin-bottom: 0;
        &.active {
          color: #666;
          background: #f4f6f8;
          cursor: no-drop;
          padding-left: 10px;
        }
      }
    }
    .verify {
      font-size: 14px;
      color: #f02626;
      display: none;
      line-height: 14px;
      margin-bottom: 20px;
      margin-top: 2px;
      &::after {
        content: " ";
        display: inline-block;
        width: 14px;
        height: 14px;
        background: url("../../../assets/image/erriput.png") no-repeat;
        background-size: 100% 100%;
        vertical-align: middle;
        margin-right: 7px;
        float: left;
      }
    }
  }

  //
  .iphoneLogin {
    .otherLogin {
      font-size: 0;
      color: #666;
      & > span {
        font-size: 14px;
        cursor: pointer;

        &:hover {
          color: #2878ff;
        }
      }
      & > div {
        float: right;
        font-size: 14px;
        font {
          cursor: pointer;
          &:hover {
            color: #2878ff;
          }
        }
      }
    }
  }

  //
  .registerObj {
    .otherLogin {
      font-size: 0;
      color: #666;
      & > span {
        font-size: 14px;
        font {
          font-size: 14px;
          color: #2878ff;
          cursor: pointer;
        }
      }
      & > span:nth-of-type(2) {
        float: right;
        font-size: 14px;
      }
    }
  }

  //
  .resetWordObj {
  }

  //
  .newAccObj {
    h3 {
      color: #999;
      font-size: 14px;
      position: absolute;
      top: 90px;
    }
    & > input:nth-of-type(1) {
      margin-top: 10px;
    }
    p {
      color: #2878ff;
      cursor: pointer;
      font-size: 14px;
      text-align: center;
    }
  }

  //
  .msgsuccessObj {
    text-align: center;
    margin-bottom: 70px;
    img {
      width: 50px;
      height: 50px;
      padding-top: 40px;
      padding-bottom: 30px;
    }
    p {
      font-size: 20px;
      color: #333;
      line-height: 20px;
      padding-bottom: 30px;
    }
    button {
      margin-bottom: 10px;
      width: 165px;
      height: 50px;
    }
  }
}
</style>


