<!--  -->
<template>
  <div class="program">
    <h3></h3>
    <ul>
      <li class="b-li clearfix" v-for="item in dataBottom">
        <img :src="item.src" alt />
        <article>
          <dt :title="item.title" class="moreMWord">{{item.title}}</dt>
          <dd>{{item.remark}}</dd>
          <dd class="s-star middle" v-html="item.stars"></dd>
        </article>
      </li>
    </ul>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  data() {
    //
    return {
      dataBottom: [
        {
          src: require("@/assets/image/b3.png"),
          title: "",
          remark: "",
          see: 3,
          editStar: 2
        },
        {
          src: require("@/assets/image/b2.png"),
          title: "",
          remark: "",
          see: 4,
          editStar: 3.5
        }
      ]
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {},
  // - this
  created() {},
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
.program {
  h3 {
    font-size: 24px;
    line-height: 24px;
    color: #333;
    margin-bottom: 30px;
  }
  ul {
    .b-li {
      margin-bottom: 36px;
      height: 110px;
      cursor: pointer;
      &:last-of-type {
        margin-bottom: 0;
      }
      img {
        float: left;
        width: 110px;
        height: 110px;
        border: 1px solid #f4f6f8;
        box-sizing: border-box;
        border-radius: 10px;
      }
      article {
        margin-left: 140px;
        position: relative;
        height: 100%;
        dt {
          font-size: 18px;
          color: #333;
          line-height: 18px;
        }
        dd:nth-of-type(1) {
          margin: 12px 0 20px 0;
          font-size: 14px;
          color: #999;
        }
      }
    }
  }
}
</style>


