<!--  -->
<template>
  <!-- <div class="layout"></div> -->
  <ul class="tags clearfix">
    <template v-for="item,i in navTab">
      <router-link tag="li" v-if="!nav.length==i" to="/Home" class="active">{{item.name}}</router-link>
      <router-link tag="li" v-else to="/Home" class="active">{{item.name}}</router-link>
    </template>
  </ul>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  props: ["navTab"],
  data() {
    //
    return {
      navTab: [{ name: "" }, { name: "" }]
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {},
  // - this
  created() {},
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
$c9: #999;
$c6: #666;
$c3: #333;
$b: #2878ff;
$f4: #f4f6f8;
// .layout {
//   width: 1240px;
//   margin: 0 auto;
.tags {
  padding-top: 30px;
  li {
    float: left;
    margin-right: 5px;
    color: $c6;
    cursor: pointer;
    font-size: 14px;
    line-height: 14px;
    &.active {
      color: $b;
    }
  }
}
// }
</style>


