<!--  -->
<template>
  <div class="deserve">
    <h3></h3>
    <ul>
      <li class="t-li clearfix" v-for="item in dataTop">
        <img :src="item.src" alt />
        <article>
          <dt :title="item.title" class="moreMWord">{{item.title}}</dt>
          <dd class="count clearfix">
            <span>{{item.time}}</span>
            <span>{{item.see}}</span>
          </dd>
        </article>
      </li>
    </ul>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  data() {
    //
    return {
      dataTop: [
        {
          src: require("@/assets/image/info/article1.png"),
          title:
            " Vue  router  router ",
          time: 3,
          see: 213213
        },
        {
          src: require("@/assets/image/info/article1.png"),
          title:
            " Vue  router  router ",
          time: 3,
          see: 213213
        }
      ]
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {},
  // - this
  created() {},
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
.deserve {
  h3 {
    font-size: 24px;
    line-height: 24px;
    color: #333;
    margin-bottom: 30px;
  }
  ul {
    .t-li {
      margin-bottom: 30px;
      height: 90px;
      cursor: pointer;
      &:last-of-type {
        margin-bottom: 0;
      }
      img {
        float: left;
      }
      article {
        position: relative;
        margin-left: 144px;
        height: 100%;
        dt {
          font-size: 16px;
          color: #333;
          line-height: 22px;
        }
        .count {
          position: absolute;
          bottom: 0;
          width: 100%;
          span {
            font-size: 14px;
            color: #999;
            line-height: 14px;
            float: left;
            &:nth-of-type(2) {
              float: right;
            }
          }
        }
      }
    }
  }
}
</style>


