<!--  -->
<template>
  <div id="layout">
    <v-Headers @isMask="isMask"></v-Headers>
    <!-- <v-Navigation v-if="mask" :navTab="navTab"></v-Navigation> -->
    <main>
      <transition name="component-fade" mode="out-in">
        <router-view @isMask="isMask"></router-view>
      </transition>
    </main>
    <v-Footers></v-Footers>
    <div class="m-mask" v-if="mask"></div>
    <v-Popup></v-Popup>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import Headers from "@/components/common/headers.vue";
import Footers from "@/components/common/footers.vue";
import Popup from "@/components/common/plugin/popup.vue";
import vueEvent from "./model/vueEvent";
import Navigation from "@/components/common/plugin/navigation.vue";

export default {
  //import

  components: {
    "v-Headers": Headers,
    "v-Footers": Footers,
    "v-Popup": Popup,
    "v-Navigation": Navigation
  },
  data() {
    //
    return {
      mask: false,
      navTab: [],
      historyUrl: [],
      routerArr: [
        "Home",
        "AppCollection",
        "ReadArticle",
        "EditApplet",
        "EditPublicA",
        "MiniProgram", //
        "ManyArticles"
      ]
    };
  },
  // data
  computed: {},
  //data
  watch: {
  },
  //
  methods: {
    isMask(flag) {
      // console.log(this.mask);
      this.mask = flag;
    },
    navTabFun(navTab) {
      this.navTab = navTab;
    }
  },
  // - this
  created() {
      console.log(1);

  },
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
//@import url(); css
.component-fade-enter-active,
.component-fade-leave-active {
  transition: opacity 0.24s ease;
}
.component-fade-enter, .component-fade-leave-to
/* .component-fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>


